<template>
    <div class="address-edit">
        <div class="address-list">
        <cube-scroll>
            <ul class="list">
                <li class="item">
                    <div class="text">收货人</div>
                    <div class="content">
                        <cube-input v-model="currentAddress.receiverName"></cube-input>
                        <cube-validator ref="validator0" v-model="result[0]" :model="currentAddress.receiverName" :rules="receiverNameRules"></cube-validator>
                    </div>
                </li>
                <li class="item">
                    <div class="text">手机号码</div>
                    <div class="content">
                        <cube-input v-model="currentAddress.phone"></cube-input>
                        <cube-validator ref="validator1" v-model="result[1]" :model="currentAddress.phone" :rules="phoneRules"></cube-validator>
                    </div>
                </li>
                <li class="item" @click="showAddressPicker">
                    <div class="text">所在地区</div>
                    <div class="content">
                        {{currentAddress.province}}
                        {{currentAddress.city}}
                        {{currentAddress.area}}
                        <cube-validator ref="validator3" v-model="result[3]" :model="currentAddress.province" :rules="provinceRules"></cube-validator>
                    </div>
                    <div> > </div>
                </li>
                <li class="item">
                    <div class="text">详细地址</div>
                    <div class="content">
                        <cube-textarea class="detail-address" v-model="currentAddress.address" @blur="_blur" placeholder="请输入"></cube-textarea>
                        <cube-validator ref="validator2" v-model="result[2]" :model="currentAddress.address" :rules="addressRules"></cube-validator>
                    </div>
                </li>
                <li class="item">
                    <div class="text">设置默认</div>
                    <cube-switch v-model="hasDefault"></cube-switch>
                </li>
            </ul>
        </cube-scroll>
        </div>
        <div class="address-add">
            <div class="btn" @click="_save()">保存</div>
        </div>
        <cube-drawer
            class="order-page"
            ref="drawer"
            title="请选择"
            :data="data"
            :selected-index="selectedIndex"
            @change="changeHandler"
            @select="selectHandler"
            @cancel="cancelHandler">
        </cube-drawer>
    </div>
</template>
<script>
import { queryReceiveArea, queryAreaList, updateReceiverAddress, receiverAddressDetail, addReceiverAddress } from '@/api/address.js';
export default {
    name: 'address-edit',
    data() {
        return {
            hasDefault: false,
            selectedIndex: [],
            data: [
                [],
                [],
                []
            ],
            currentAddress: {
                address: '',
                area: '',
                city: '',
                hasDefault: 0,
                id: 0,
                phone: '',
                province: '',
                receiverName: '',
                street: '',
                userId: 1,
            },
            result: [undefined, undefined, undefined, undefined],
            receiverNameRules: {
                required: true,
            },
            phoneRules: {
                required: true,
                type: 'tel',
            },
            addressRules: {
                required: true,
            },
            provinceRules: {
                required: true,
            },
        }
    },
    created() {
        this._queryAreaList(0, 0);
        if (this.$route.query.id !== undefined) {
            // this._queryReceiveArea();
            this._receiverAddressDetail(this.$route.query.id);
        }
    },
    methods: {
        // 地址详情
        _receiverAddressDetail(id) {
            let params = {
                id,
            }
            receiverAddressDetail(params).then(res => {
                this.currentAddress = res;
            })
        },
        // 所有地址
        _queryReceiveArea() {
            let params = {
                userId: 1,
            }
            queryReceiveArea(params).then(res => {
                this.currentAddress = res.find(item => item.id === Number(this.$route.query.id));
                this.hasDefault = this.currentAddress.hasDefault === 1 ? true : false;
            })
        },
        // 省市区
        _queryAreaList(code, index) {
            let params = {
                areaCode: code
            }
            queryAreaList(params).then(res => {
                let arr = res || [];
                arr.forEach(item => {
                    this.data[index].push({
                        text: item.areaDesc,
                        value: item.areaCode,
                    })
                })
            })
        },
        // 选择地点
        showAddressPicker() {
            this.$refs.drawer.show();
        },
        changeHandler(index, item, selectedVal, selectedIndex, selectedText) {
            this._queryAreaList(item.value, index + 1);
            this.$refs.drawer.refill(index + 1, this.data[index + 1]);
        },
        selectHandler(selectedVal, selectedIndex, selectedText) {
            this.currentAddress.province = selectedText[0];
            this.currentAddress.city = selectedText[1];
            this.currentAddress.area = selectedText[2];
        },
        cancelHandler() {
            console.log('cancel')
        },
        _blur() {
            setTimeout(function () {
                let scrollHeight = document.documentElement.scrollTop || document.body.scrollTop || 0;
                window.scrollTo(0, Math.max(scrollHeight - 1, 0));
            }, 100);
        },
        _save() {
            const p0 = this.$refs.validator0.validate();
            const p1 = this.$refs.validator1.validate();
            const p2 = this.$refs.validator2.validate();
            const p3 = this.$refs.validator3.validate();
            Promise.all([p0, p1, p2, p3]).then(() => {
                if (this.result.every(item => item)) {
                    if (this.$route.query.id !== undefined) {
                        this._edit();
                    } else {
                        this._add();
                    }
                }
            })        
        },
        // 新增
        _add() {
            this.currentAddress.userId = 1;
            if (this.hasDefault) {
                this.currentAddress.hasDefault = 1;
            } else {
                this.currentAddress.hasDefault = 0;
            }
            addReceiverAddress(this.currentAddress).then(res => {
                this.toast = this.$createToast({
                    txt: '操作成功',
                    type: 'txt',
                    time: 500,
                    onTimeout: () => {
                        this.$router.go(-1);                        
                    }
                })
                this.toast.show()
            })
        },
        // 编辑
        _edit() {
            delete this.currentAddress.gmtCreate;
            delete this.currentAddress.gmtModify;
            if (this.hasDefault) {
                this.currentAddress.hasDefault = 1;
            } else {
                this.currentAddress.hasDefault = 0;
            }
            updateReceiverAddress(this.currentAddress).then(res => {
                this.toast = this.$createToast({
                    txt: '操作成功',
                    type: 'txt',
                    time: 500,
                    onTimeout: () => {
                        this.$router.go(-1);                        
                    }
                })
                this.toast.show()
            })
        },
    }
}
</script>
<style lang="stylus" scoped>
.address-edit {
    height: 100vh;
    font-family: HiraginoSansGB-W3;
    background-color: #ffffff;
    .address-list {
        height: calc(100vh - 120px);
        .list {
            padding: 0 12px;
            .item {
                min-height: 98px;
                padding: 24px 0;
                border-bottom: 0.5px solid #F2F2F2;
                display: flex;
                align-items: center;
                justify-content: space-between;
                position: relative;
                .text {
                    width: 120px;
                    margin: 0 16px;
                    font-size: 28px;
                    color: #333333;    
                }
                .content {
                    flex: auto;                
                    text-align: left;
                    color: #CCCCCC;
                }
            }
        }
    }
    .address-add {
        height: 120px;
        .btn {
            margin: 10px auto;
            width: 600px;
            height: 98px;
            line-height: 98px;
            color: #ffffff;
            text-align: center;
            background:rgba(74,76,89,1);
            border-radius:10px;
        }
    }
}
</style>


